export const handleKeyboardNavigation = (
  event,
  checkboxes,
  updateCheckCount,
  isAlternative
) => {
  const { key } = event;
  const currentIndex = checkboxes.findIndex(
    (checkbox) => checkbox === event.target
  );

  let newIndex = -1;

  if (key === "ArrowDown" || key === "ArrowRight") {
    newIndex = currentIndex + 1;
  } else if (key === "ArrowUp" || key === "ArrowLeft") {
    newIndex = currentIndex - 1;
  } else if (key === "Home") {
    newIndex = 0;
  } else if (key === "End") {
    newIndex = checkboxes.length - 1;
  }

  if (isAlternative && (key === " " || key === "Enter")) {
    event.preventDefault();
    updateCheckCount(event);
  }

  if (newIndex >= 0 && newIndex < checkboxes.length) {
    event.preventDefault();
    checkboxes[newIndex].focus();
    if (isAlternative) {
      updateCheckCount({ target: checkboxes[newIndex] });
    }
  }
};